</template>


<script>
export default {
  data() {
    return {
      show: false,
      bookcase: [],
      bookSwitch: false,
      deleteBtn: false,
    };
  },
  created() {
    this.bookcase = JSON.parse(localStorage.getItem("bookcase")) || [];
    console.log(this.bookcase);
  },
  methods: {
    isShowMore() {
      this.show = !this.show;
    },

    test() {
      console.log("in");
    },
    async getLastChapter(id) {
      let { data } = await this.$axios.get(
        `https://www.kanman.com/api/getComicInfoAttribute?comic_id=${id}`
      );
      console.log(data.data);
      return data.data.last_chapter_name;
    },
    turnToRead(id, newId) {
      this.$router.push({
        path: "/read",
        query: {
          id: id,
          chapterId: newId,
        },
      });
    },
    deleteBook() {
      console.log(1);
      this.show = true;
      this.bookSwitch = true;
      this.deleteBtn = true;
      console.log(this.bookSwitch);
    },
    initHead() {
      this.bookSwitch = false;
      this.deleteBtn = false;
    },
    removeBook(id) {
      this.bookcase = this.bookcase.filter((el) => {
        return el.bookId !== id
      });
      localStorage.setItem('bookcase',JSON.stringify(this.bookcase))
    },
    turnToSearch(){
      this.$router.push({
        path:'/home/search'
      })
    }
  },
};
</script>


<style lang="scss">
.bg {
  position: relative;
  .mask {
    height: 100vh;
    width: 100vw;
    background-color: rgb(0, 0, 0, 0.3);
    position: absolute;
    top: 0%;
    left: 0%;
    right: 0%;
    bottom: 0%;
    z-index: 1000;
  }

  header {
    width: 100vw;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;

    .search {
      margin-left: 20px;
      position: relative;

      &::after {
        content: "";
        display: inline-block;
        width: 10px;
        height: 10px;
        background-color: red;
        border-radius: 999px;
        position: absolute;
        top: -10px;
        right: -10px;
      }
    }
    .more {
      margin-right: 20px;
      position: relative;

      .moreList {
        width: 35vw;
        height: 80px;
        position: absolute;
        top: 45px;
        right: -20px;
        background-color: #fff;
        border-radius: 10px;
        z-index: 1001;
        ul {
          width: 100%;
          height: 100%;
          li {
            margin-top: 5px;
            width: 100%;
            height: 30px;
            font-size: 15px;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
    .turn-off {
      font-size: 16px;
      margin-right: 13px;
    }
  }

  main {
    .comicItem {
      position: relative;
      width: 100%;
      height: 15vh;
      display: flex;
      justify-content: space-between;
      margin-top: 20px;

      .comicItemImg {
        height: 100%;
        width: 25%;
        background-color: aquamarine;
        img {
          width: 100%;
        }
      }

      .comicItemText {
        width: 75%;
        height: 100%;
        padding-left: 10px;

        p {
          margin-top: 15px;
          color: #aaa;
          font-size: 14px;
        }

        .title {
          color: #000;
          font-size: 18px;
          font-weight: 700;
        }
      }
      .delete {
        position: absolute;
        right: 10px;
        top: 0px;
        i {
          color: red;
        }
      }
    }
  }
}
</style>